
.page-loading {
  position: fixed;
  z-index: 99999999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
}
.page-loading-img {
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  margin-left: -0.53333333rem;
  margin-top: -0.53333333rem;
  width: 1.06666667rem;
  height: 1.06666667rem;
}



